import React, { Component } from 'react';
import Layouts from '../component/Layouts'
import IndexTable from '../component/Table/IndexTable'
import { Button } from '@material-ui/core'
import DelBankDataFromLocal from '../app/lib/DelBankDataFromLocal'
import Link from 'next/link'

class Index extends Component {
    state = {
        res: []
    }

    componentDidMount() {
        this.getData()
    }

    getData() {
        this.setState({
            res: JSON.parse(localStorage.getItem('bank')),
        })
    }

    handleDelete = (params) => {
        DelBankDataFromLocal({
            id: params
        })
    }

    render() {
        return (
            <Layouts title='Bank Central Dion'>
                <div className="container">
                    <Link href="/AddBankData">
                        <Button
                            className='m-bgs'
                            variant="contained"
                            color="primary"
                        >+ Add</Button>
                    </Link>
                    <IndexTable
                        rows={this.state.res}
                        handleDelete={this.handleDelete}
                    />
                </div>
            </Layouts>
        );
    }
}

export default Index;
